/* Over lay effect */

.overlay {
  position: absolute;
  bottom: 0;
  left: 100%;
  right: 0;
 
  overflow: hidden;
  width: 0;
  height: 100%;
  transition: .3s ease;
  border-radius: 12px;
}

.overlay-text {
  font-weight: 300;
  color: #2C4677;
  font-size: 17px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

button:hover .overlay {
  width: 100%;
  left: 0;
}

